.main {
  /* min-height: 100vh; */
  /* height: calc(100vh - 176px); */
  margin: 10px auto;
  width: 100%;
  padding: 10px;
}

section {
  min-height: calc(100vh - 30px);
  line-height: 1.1em;
}

::-webkit-scrollbar-track {
  border-radius: 2px;
  background-color: #3b3b3b;
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
}

::-webkit-scrollbar {
  width: 12px;
  background-color: #3b3b3b;
}

::-webkit-scrollbar-thumb {
  border-radius: 3px;
  background: #5f646b;
}

h2 {
  font-family: 'Calibre', 'San Francisco', 'SF Pro Text', -apple-system,
    system-ui, sans-serif;
}

.otherContent {
  font-size: 1.3rem;
  line-height: 1.2em;
  margin: 10px 2px 10px 2px;
  font-weight: normal;
}

.aboutme {
  display: flex;
  justify-content: left;
  flex-direction: row;
  align-items: center;
  min-height: 100vh;
  line-height: 1.3em;
}

.projects {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: flex-start;
}

.centered {
  /* display: flex;
  justify-content: center;
  flex-direction: row;
  align-items: center; */
  margin: 20px auto;
  width: 82%;

  /* border: 1px solid skyblue; */
}

.name {
  color: #8892b0;
  font-size: larger;
  margin-top: 10px;
}

.aboutme .hireme {
  font-style: italic;
  color: cadetblue;
  margin-bottom: 14px;
}

.aboutme .connectLink {
  text-decoration: underline;
  font-size: 1rem;
  display: none;
}

.aboutme .connectLink1 {
  border: 1px solid cadetblue;
  border-radius: 3px;
  padding: 12px;
  text-decoration: underline;
  cursor: pointer;
  margin-top: 30px;
  width: 125px;
}

.aboutme .connectLink a {
  color: cadetblue !important;
}

.scroller {
  scroll-snap-type: y mandatory;
  -webkit-overflow-scrolling: touch;
}

.scroller section {
  scroll-snap-align: start;
}

.blogs {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
  grid-column-gap: 10px;
  grid-row-gap: 10px;
  padding-bottom: 300px;
}

.blogs section {
  height: 100%;
}

.card {
  border-radius: 0px !important;
  border: 1px solid rgba(255, 255, 255, 0.1);
  box-shadow: 1px 1px 4px #000000;
  text-rendering: optimizeLegibility;
}

.card-header {
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
  background-color: #1a202990;
}

.card-title {
  font-size: 1.3rem;
  line-height: 1.1em;
  color: #ccd6f6;
}

.card-body {
  line-height: normal;
  background-color: #1a202d;
  color: #fffffc !important;
  font-size: 1.2rem;
}

.card-body a {
  color: #88b6d1;
  font-size: 1rem !important;
  text-decoration: underline;
}
