.footer {
  background-color: #1f293b;
  position: fixed;

  right: 0px;
  left: 0px;
  bottom: 0px;
  width: 100vw;
}

.footer .centered {
  display: flex;
  flex-flow: column wrap;
  justify-content: space-between;
  align-items: center;
  text-align: center;
  margin: 12px auto;
  padding-left: 0px;
  width: 82%;
}

@media screen and (min-width: 600px) {
  .footer .centered {
    flex-flow: row wrap;
  }
}

.dropdown-menu.show,
.dropdown-toggle {
  background-color: #1a263d !important;
  border-radius: 0px !important;
  font-size: 0.9em !important;
}

.dropdown-item {
  color: #fcfcff !important;
}

.dropdown-item:hover,
.dropdown-item:focus {
  color: #fcfcff !important;
  background-color: #4e6796 !important;
}
